import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import {
  FormContainer,
  FormTitle,
  FormWrap,
  FormWrapText,
  Form,
  FormText,
  FormText2,
  FormPar,
  Con,
  InputLine,
  TextArea,
  ButtonGroup,
  AttachButton,
  SendButton,
  Attach,
} from './MainForm.styled';
import AttachIcon from '../../image/Vector66.svg';
import { ArrowLink } from 'components/AboutUs/AboutUs.styled';
import Arrow from '../../image/Vector65.svg';
import { SendText } from 'components/ContactUsModal/ContactUsModal.styled';

//  ScrollTrigger
gsap.registerPlugin(ScrollTrigger);

const MainForm = () => {
  useEffect(() => {
    //   FormTitle ( )
    gsap.fromTo(
      '.form-title',
      { y: -100, opacity: 0 },
      {
        y: 0,
        opacity: 1,
        duration: 2,
        scrollTrigger: {
          trigger: '.form-title',
          start: 'top 50%',
          toggleActions: 'play none none none',
        },
      }
    );

    //   
    gsap.fromTo(
      '.form-text',
      { x: -200, opacity: 0 },
      {
        x: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: '.form-text',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );

    //   
    gsap.fromTo(
      '.form',
      { x: 200, opacity: 0 },
      {
        x: 0,
        opacity: 1,
        duration: 1,
        scrollTrigger: {
          trigger: '.form',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );
  }, []);
  const [formData, setFormData] = useState({
    name: '',
    contact: '',
    message: '',
  });

  const handleFileChange = e => {
    const file = e.target.files[0];
    setFormData(prevData => ({
      ...prevData,
      file,
    }));
  };

  const handleChange = e => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    try {
      await axios.post('http://localhost:5000/send', formData);
      alert('  !    `  !');
    } catch (error) {
      console.log(error);
      alert('!    ! ');
    }

    setFormData({
      name: '',
      contact: '',
      message: '',
    });
  };
  return (
    <FormContainer id="contact">
      <FormTitle className="form-title">
         <br />  <br />
         .
      </FormTitle>
      <FormWrap>
        <FormWrapText className="form-text">
          <FormText>
                <br />
          </FormText>
          <FormText2>
                  <br />   
             
          </FormText2>
          <FormPar>
                ,    
             .       
                    
            .
          </FormPar>
        </FormWrapText>
        <Form className="form" onSubmit={handleSubmit}>
          <Con>
            <InputLine
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
            />
            <InputLine
              type="text"
              name="contact"
              value={formData.contact}
              onChange={handleChange}
            />
            <TextArea
              type="text"
              name="message"
              value={formData.message}
              onChange={handleChange}
            />
          </Con>
          <ButtonGroup>
            <AttachButton>
              <Attach alt="AttachIcon" src={AttachIcon} />
            </AttachButton>
            <SendButton type="submit">
              <SendText></SendText> <ArrowLink src={Arrow} />
            </SendButton>
          </ButtonGroup>
        </Form>
      </FormWrap>
    </FormContainer>
  );
};

export default MainForm;
