import { BurgerIcon } from 'components/Welcome/Welcome.styled';
import {
  ArrowContLinkPWA,
  ArrowLinkPWA,
  ButtonsContainer,
  FileIcon,
  FileInputButton,
  Form,
  FormBlock,
  FormConteiner,
  InputField,
  MainPwaText,
  PWAContainer,
  PWAGreenOverlay,
  PWAImageContainer,
  PWAImagesContainer,
  PWALeftImage,
  PWAMainTitle,
  PWARightImage,
  PWAStarContainer,
  PWAStarImg,
  PWASubTItle,
  PWAText,
  PwaTextForm,
  PWATextOverlay,
  SubmitButton,
  TextAreaField,
  TextPwaConteiner,
  TitlePwaText,
} from './PWA.styled';
import { BackArrow } from '../Services.styled';
import SideBarMenu from 'components/SideBarMenu/SideBarMenu';
import { useEffect, useState } from 'react';
import gsap from 'gsap';
import screen_one from '../../../image/screen_one.png';
import screen_two from '../../../image/screen_two.png';
import star from '../../../image/Star.svg';
import { NavLink } from 'react-router-dom';
import Arrow from '../../../image/Vector65.svg'
import AttachIcon from '../../../image/Vector66.svg';
import WhiteArrow from '../../../image/VectorWhite.svg'



const PWA = () => {
  const [sidebarSwitcher, setSidebarSwitcher] = useState(false); //     
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
    file: null,
  });

  const handleInputChange = e => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleFileChange = e => {
    const file = e.target.files[0];
    setFormData(prevData => ({
      ...prevData,
      file,
    }));
  };

  const handleSubmit = e => {
    e.preventDefault();
    console.log(' :', formData);
    setFormData({
      name: '',
      email: '',
      message: '',
    });
  };

  const onClickGood = () => {
    setSidebarSwitcher(prevState => !prevState); //   
  };

  useEffect(() => {
    //     ()
    gsap.set('.sideMenu', { x: '-100vw' });
    gsap.to('.star', {
      rotation: '+=360', //    
      duration: 5,
      repeat: -1, //  
      ease: 'linear', //  
    });
  }, []);

  return (
    <PWAContainer>
      <BurgerIcon onClick={onClickGood} className="good" />
      <NavLink to="/" style={{ textDecoration: 'none' }}>
        <BackArrow />
      </NavLink>
      <SideBarMenu isOpen={sidebarSwitcher} />

      <PWAMainTitle>PIWA</PWAMainTitle>
      <PWASubTItle>PROGRESSIVE INCREDIBLE WEB APPS</PWASubTItle>

      <PWAImagesContainer>
        <PWAImageContainer isLeft>
          <>
            <PWALeftImage src={screen_one} />
            <PWAGreenOverlay />
          </>
          <PWATextOverlay>CODEBEARS <ArrowLinkPWA src={WhiteArrow}/></PWATextOverlay>
        </PWAImageContainer>
        <PWAImageContainer>
          <>
            <PWARightImage src={screen_two} />
            <PWAGreenOverlay />
          </>
          <PWATextOverlay>KN.MYSTRN <ArrowLinkPWA src={WhiteArrow}/></PWATextOverlay>
        </PWAImageContainer>
      </PWAImagesContainer>

      <PWAText>
        Progressive Web Apps (PWA)   -,   
            .   ,
             ,   
           .   
            PWA,     
        ,    ,    
            .  ,  
         ,      .
      </PWAText>
      <PWAStarContainer>
        <PWAStarImg src={star} className="star" />
      </PWAStarContainer>
      <FormBlock>
        <TextPwaConteiner>
          <MainPwaText> ?</MainPwaText>
          <TitlePwaText>
                    
          </TitlePwaText>
          <PwaTextForm>
                ,    
             .       
                    
            .
          </PwaTextForm>
        </TextPwaConteiner>
        <FormConteiner>
          <Form>
            <InputField
              type="text"
              name="name"
              // placeholder=" "
              value={formData.name}
              onChange={handleInputChange}
            />
            <InputField
              type="email"
              name="email"
              // placeholder=" email"
              value={formData.email}
              onChange={handleInputChange}
            />
            <TextAreaField
              name="message"
              // placeholder=" "
              value={formData.message}
              onChange={handleInputChange}
            />
          </Form>
          <ButtonsContainer>
            <FileInputButton>
              <label htmlFor="file">
                <FileIcon src={AttachIcon}/>
              </label>
              <input
                type="file"
                id="file"
                name="file"
                onChange={handleFileChange}
              />
            </FileInputButton>
            <SubmitButton onClick={handleSubmit}>
              
              <ArrowContLinkPWA src={Arrow}/>
            </SubmitButton>
          </ButtonsContainer>
        </FormConteiner>
      </FormBlock>
    </PWAContainer>
  );
};

export default PWA;
