import { useEffect, useRef, useState } from 'react';
import {
    Body,
    BurgerIcon,
    SideBarContainer,
    Span,
    Stars,
    Stars2,
    Stars3,
    StarsWrapper,
    TextContainer,
    Title,
    WelcomeContainer,
} from './Welcome.styled';
import gsap from 'gsap';
import bez_glaz from '../../image/bez_glaz.svg';
import glaza from '../../image/gla3a.svg';
import SideBarMenu from 'components/SideBarMenu/SideBarMenu';

const Welcome = () => {
    const [offset, setOffset] = useState({ x: 0, y: 0 });
    const text = 'CODEBEARS CODEBEARS CODEBEARS CODEBEARS ';

    const [sidebarSwitcher, setSidebarSwitcher] = useState(false); //     
    const titleRef = useRef(null); //     Title

    const onClickGood = () => {
        setSidebarSwitcher(prevState => !prevState); //   
    };

    useEffect(() => {
        //     ()
        gsap.set('.sideMenu', { x: '-100vw' });
    }, []);

    useEffect(() => {
        //    Title
        gsap.fromTo(
            titleRef.current, //    
            { opacity: 0, scale: 0.5 }, //  
            {
                opacity: 1,
                scale: 1,
                duration: 1.5,
                ease: 'power3.out', //  
                delay: 0.5, //   
            }
        );
    }, []);

    useEffect(() => {
        const handleMouseMove = e => {
            //   (   )
            const eyeCenterX = window.innerWidth / 2;
            const eyeCenterY = window.innerHeight / 2;

            //     
            const mouseX = e.clientX - eyeCenterX;
            const mouseY = e.clientY - eyeCenterY;

            //    
            const maxDistance = 10; //     
            const distance = Math.sqrt(mouseX ** 2 + mouseY ** 2); //    

            //  ,      maxDistance
            const scale = Math.min(maxDistance / distance, 1);

            setOffset({
                x: mouseX * scale,
                y: mouseY * scale,
            });
        };

        //    
        window.addEventListener('mousemove', handleMouseMove);

        //     
        return () => {
            window.removeEventListener('mousemove', handleMouseMove);
        };
    }, []);

    return (
        <WelcomeContainer>
            <BurgerIcon onClick={onClickGood} className="good" />
            <SideBarMenu isOpen={sidebarSwitcher} onClickGood={onClickGood} />

            <Title
                id="title"
                className="face-container title"
                ref={titleRef} //    
            >
                <img
                    src={bez_glaz}
                    alt="Face"
                    style={{
                        display: 'block',
                    }}
                />
                {/*  */}
                <img
                    src={glaza}
                    alt="Eyes"
                    style={{
                        position: 'absolute',
                        transform: `translate(0, 0) translate(${offset.x}px, ${offset.y}px)`,
                        transition: 'transform 0.15s ease-out',
                    }}
                />
                <Body style={{ position: 'absolute' }}>
                    <TextContainer>
                        {text.split('').map((char, index) => (
                            <Span key={index} angle={index * (360 / text.length)}>
                                {char}
                            </Span>
                        ))}
                    </TextContainer>
                </Body>
            </Title>
        </WelcomeContainer>
    );
};

export default Welcome;
