import {
  ButtonItem,
  ButtonsContainer,
  HeroContainer,
  HeroMainText,
  HeroSubText,
  MoonImg,
  StarImg,
  StartContainer,
} from './Hero.styled';
import moon from '../../image/moon.png';
import star from '../../image/Star.svg';
import { useEffect } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { NavLink } from 'react-router-dom';
gsap.registerPlugin(ScrollTrigger);



const Hero = () => {
  useEffect(() => {

    //   ScrollTrigger

    //     (   )
    gsap.set('.moon', { y: '0' });
    gsap.to('.moon', {
      rotation: 360,
      duration: 30,
      repeat: -1,
      ease: 'linear',
    });
    gsap.to('.star', {
      rotation: "+=360", //    
      duration: 5,
      repeat: -1, //  
      ease: 'linear', //  
    });

    gsap.to('.star2', {
      rotation: "-=360", //    
      duration: 5,
      repeat: -1,
      ease: 'linear',
    });

    //    
    gsap.fromTo(
      '.hero-main-text',
      { y: 50, opacity: 0 },
      {
        y: 0,
        opacity: 1,
        duration: 1,
        ease: 'power3.out',
        scrollTrigger: {
          trigger: '.hero-main-text',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );

    gsap.fromTo(
      '.hero-sub-text',
      { y: 50, opacity: 0 },
      {
        y: 0,
        opacity: 1,
        duration: 1,
        ease: 'power3.out',
        scrollTrigger: {
          trigger: '.hero-sub-text',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );

    //    
    gsap.fromTo(
      '.star',
      { scale: 0, rotation: 0, opacity: 0 },
      {
        scale: 1,
        rotation: 360,
        opacity: 1,
        duration: 1.2,
        scrollTrigger: {
          trigger: '.star',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );

    gsap.fromTo(
      '.star2',
      { scale: 0, rotation: 0, opacity: 0 },
      {
        scale: 1,
        rotation: -360,
        opacity: 1,
        duration: 1.2,
        scrollTrigger: {
          trigger: '.star2',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );

    //    
    gsap.fromTo(
      '.buttons-container',
      { y: 100, opacity: 0 },
      {
        y: 0,
        opacity: 1,
        duration: 1,
        stagger: 0.2, //     
        ease: 'power3.out',
        scrollTrigger: {
          trigger: '.buttons-container',
          start: 'top 80%',
          toggleActions: 'play none none none',
        },
      }
    );
  }, []);

  return (
    <HeroContainer className="text-block">
      <HeroMainText className="hero-main-text">
         /  / .
      </HeroMainText>
      <StartContainer>
        <StarImg src={star} className="star" />
        <HeroSubText className="hero-sub-text">
             ,    ,  
          .
        </HeroSubText>
        <StarImg src={star} className="star2" />
      </StartContainer>
      <ButtonsContainer className="buttons-container">
        <NavLink to="/portfolio" style={{ textDecoration: 'none' }}>
          <ButtonItem></ButtonItem>
        </NavLink>
        <ButtonItem></ButtonItem>
        <NavLink to="/services" style={{ textDecoration: 'none' }}>
          <ButtonItem></ButtonItem>
        </NavLink>
      </ButtonsContainer>
      <MoonImg src={moon} className="moon" />
    </HeroContainer>
  );
};

export default Hero;
