import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import {
  WhyWeContainer,
  WhyWeMainTitle,
  WhyWeSubTitle,
  WhyWeList,
  WhyWeItem,
  WhyWeItemTitle,
  WhyWeItemSubTitle,
} from './WhyWe.styled';

const WhyWe = () => {
  const [isVisible, setIsVisible] = useState(false); //  
  const [hasAnimated, setHasAnimated] = useState(false); //    
  const whyWeRef = useRef(null); //   

  //  Intersection Observer    
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && !hasAnimated) {
          setIsVisible(true); //     
          setHasAnimated(true); // ,   
        }
      },
      { threshold: 0.5 } // ,  50%  
    );
  
    const currentRef = whyWeRef.current; //    
  
    if (currentRef) {
      observer.observe(currentRef); //  
    }
  
    return () => {
      if (currentRef) {
        observer.unobserve(currentRef); //  
      }
    };
  }, [hasAnimated]); //   hasAnimated

  //    
  useEffect(() => {
    if (isVisible) {
      gsap.fromTo(
        '.why-left',
        { x: '-100vw', opacity: 0 }, //  
        { x: '0', opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
      gsap.fromTo(
        '.why-right',
        { x: '100vw', opacity: 0 }, //  
        { x: '0', opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
      gsap.fromTo(
        '.why-bottom',
        { y: '100vw', opacity: 0 }, //  
        { y: '0', opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
    }
  }, [isVisible]);

  return (
    <WhyWeContainer id="WhyWe" ref={whyWeRef}>
      <WhyWeMainTitle className="why-left">?</WhyWeMainTitle>
      <WhyWeSubTitle className="why-right">  CODEBEARS!</WhyWeSubTitle>
      <WhyWeList className="why-bottom">
        <WhyWeItem>
          <WhyWeItemTitle>Legal Processing.</WhyWeItemTitle>
          <WhyWeItemSubTitle>
            We love creativity, but do not forget about the objective reality, all our business
            deals are designed in such a way that each party gets the desired result.
          </WhyWeItemSubTitle>
        </WhyWeItem>
        <WhyWeItem>
          <WhyWeItemTitle>Legal Processing.</WhyWeItemTitle>
          <WhyWeItemSubTitle>
            We love creativity, but do not forget about the objective reality, all our business
            deals are designed in such a way that each party gets the desired result.
          </WhyWeItemSubTitle>
        </WhyWeItem>
        <WhyWeItem>
          <WhyWeItemTitle>Legal Processing.</WhyWeItemTitle>
          <WhyWeItemSubTitle>
            We love creativity, but do not forget about the objective reality, all our business
            deals are designed in such a way that each party gets the desired result.
          </WhyWeItemSubTitle>
        </WhyWeItem>
      </WhyWeList>
    </WhyWeContainer>
  );
};

export default WhyWe;
