import React, { useEffect } from 'react';
import gsap from 'gsap'; //  gsap  
import {
  SideBarContainer,
  SideBarMenuList,
  SideBarListLeft,
  SideBarListRight,
  SideBarListLeftItem,
  SideBarListRightLeft,
  SideBarListRightRight,
  SideBarListRightLeftItems,
  SideBarListRightRightItems,
  FixedBottomBlock,
  FixedBottomBlockText,
} from './SideBarMenu.styled'; //    
import { Link, NavLink } from 'react-router-dom';

const SideBarMenu = ({ isOpen, onClickGood }) => {
  useEffect(() => {
    if (isOpen) {
      gsap.to('.sideMenu', {
        x: 0,
        opacity: 1,
        duration: 0.4,
      });
    } else {
      gsap.to('.sideMenu', {
        x: '-100vw',
        opacity: 0,
        duration: 0.4,
      });
    }
  }, [isOpen]); //  ,  `isOpen` 

  const scrollToAnchor = (id) => {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <>
      <SideBarContainer className="sideMenu">
        <SideBarMenuList>
          <SideBarListLeft>
            <NavLink to="/" style={{ textDecoration: 'none' }}>
              <SideBarListLeftItem></SideBarListLeftItem>
            </NavLink>
            <SideBarListLeftItem onClick={() => { scrollToAnchor('about-us'); onClickGood(!isOpen); }}> </SideBarListLeftItem>
            <NavLink to="/portfolio" style={{ textDecoration: 'none' }}>
              <SideBarListLeftItem> </SideBarListLeftItem>
            </NavLink>
            <SideBarListLeftItem onClick={() => { scrollToAnchor('contact'); onClickGood(!isOpen); }}> </SideBarListLeftItem>
          </SideBarListLeft>
          <SideBarListRight>
            <SideBarListRightLeft>
              <NavLink to="/services" style={{ textDecoration: 'none' }}>
                <SideBarListRightLeftItems></SideBarListRightLeftItems>
              </NavLink>
              <SideBarListRightLeftItems></SideBarListRightLeftItems>
              <SideBarListRightLeftItems>-</SideBarListRightLeftItems>
              <SideBarListRightLeftItems>e-Commerce</SideBarListRightLeftItems>
              <SideBarListRightLeftItems>
                 
              </SideBarListRightLeftItems>
              <SideBarListRightLeftItems>
                 
              </SideBarListRightLeftItems>
              <SideBarListRightLeftItems>
                 
              </SideBarListRightLeftItems>
            </SideBarListRightLeft>
            <SideBarListRightRight>
              <SideBarListRightRightItems></SideBarListRightRightItems>
              <SideBarListRightRightItems></SideBarListRightRightItems>
              <SideBarListRightRightItems>
                UX/UI 
              </SideBarListRightRightItems>
              <SideBarListRightRightItems> </SideBarListRightRightItems>
              <SideBarListRightRightItems>
                 
              </SideBarListRightRightItems>
            </SideBarListRightRight>
          </SideBarListRight>
        </SideBarMenuList>
        <FixedBottomBlock>
          <FixedBottomBlockText>    </FixedBottomBlockText>
        </FixedBottomBlock>
      </SideBarContainer >
    </>
  );
};

export default SideBarMenu;
