import { useEffect, useState, useRef } from 'react';
import gsap from 'gsap';
import {
  AboutUsConteiner,
  TopContainer,
  BottomContainer,
  TopLeftContainer,
  TopRightContainer,
  TextLink,
  ArrowLink,
  MainTextAboutUs,
  TextAboutUs,
  BottomOverlayContainer,
} from './AboutUs.styled';
import ContactUsModal from 'components/ContactUsModal/ContactUsModal';
import Arrow from '../../image/Vector64.svg'

const AboutUs = () => {
  const [isVisible, setIsVisible] = useState(false); //  
  const aboutUsRef = useRef(null); //    AboutUs
  const [expanded, setExpanded] = useState(false); //  

  console.log(expanded);

  //  Intersection Observer    
  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true); //   
        }
      },
      { threshold: 0.5 } // ,  50%  
    );

    if (aboutUsRef.current) {
      observer.observe(aboutUsRef.current); //  
    }

    return () => {
      if (aboutUsRef.current) {
        observer.unobserve(aboutUsRef.current); //     
      }
    };
  }, []);

  //      ,    
  useEffect(() => {
    if (isVisible) {
      gsap.fromTo(
        '.top-left',
        { x: '-100vw', opacity: 0 }, //  
        { x: '0', opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
      gsap.fromTo(
        '.top-right',
        { x: '100vw', opacity: 0 }, //  
        { x: '0', opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
      gsap.fromTo(
        '.bottom',
        { opacity: 0 }, //  
        { opacity: 1, duration: 1, ease: 'power3.out' } //  
      );
    }
  }, [isVisible]);

  //   / 
  const toggleExpand = () => {
    setExpanded(prev => !prev);
  };

  //       ""
  const handleClickLink = () => {
    setExpanded(true); //  
  };

  //       ""
  const handleButtonClick = event => {
    event.stopPropagation(); //   
    setExpanded(false); //  
  };

  return (
    <AboutUsConteiner id="about-us" ref={aboutUsRef}>
      <TopContainer>
        <TopLeftContainer className="top-left"> </TopLeftContainer>
        <TopRightContainer className="top-right">
          <MainTextAboutUs>CODEBEARS</MainTextAboutUs>
          <TextAboutUs>
               ,    ,  
            .    ,    ,
             .    ,   
            .   ,    .
          </TextAboutUs>
        </TopRightContainer>
      </TopContainer>
      <BottomContainer onClick={handleClickLink} className="bottom">
        <TextLink>
          
          <ArrowLink src={Arrow} />
        </TextLink>
        <BottomOverlayContainer expanded={expanded}>
          <ContactUsModal handleButtonClick={handleButtonClick} />
        </BottomOverlayContainer>
      </BottomContainer>
    </AboutUsConteiner>
  );
};

export default AboutUs;
